
#cv-container {
  position: relative;
  top: 10%;
  text-align: center;
}
#cv-container.monitor {
  top: 30%;
  overflow: hidden;
}
#cv-slider {
  position: relative;
  top: 50%;
}
.cv-slider-arrow, .cv-slider-arrow:hover, .cv-slider-arrow:active, .cv-slider-arrow:focus, .cv-slider-arrow:visited {
  background: none;
  border: none;
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  position: fixed;
  top: 40%;
}
.cv-organization {
  font-weight: bold;
}

